        singleAxis: {
          top: 50,
          bottom: 50,
          axisTick: {},
          axisLabel: {},
          type: 'time',
          axisPointer: {
            animation: false,
            label: {
              show: false
            }
          },
          splitLine: {
            show: false,
            lineStyle: {
              type: 'dashed',
              opacity: 0.2
            }
          }
