                  }
                ],
                global: false // 缺省为 false
              }
            },
            emphasis: {
              itemStyle: {
                shadowBlur: 20,
                shadowColor: 'rgba(0, 0, 0, 0.4)'
              }
            },
            data: [
              ['0300/01/01', 8020, ''],
              ['0500/01/01', 12586, ''],
              ['0600/01/01', 6170, ''],
              ['0800/01/01', 50195, ''],
              ['1000/01/01', 120195, ''],
              ['1127/01/01', 287114, ''],
              ['1250/01/01', 29058, ''],
              ['1300/01/01', 47375, ''],
              ['1400/01/01', 25736, ''],
              ['1450/01/01', 166957, ''],
              ['1500/01/01', 236957, ''],
              ['1650/01/01', 27700, ''],
              ['1750/01/01', 90089, ''],
              ['1900/01/01', 20367, ''],
              ['1920/01/01', 28419, ''],
              ['1970/01/01', 28219, ''],

              // ['1150/01/01', 19058, ''],


              // ['-0500/01/01', 570, ''],
              // ['-0100/01/01', 363, '']
            ]
          }
        ]
      };
      // option && myChart.setOption(option);
      myChart.setOption(option);
      window.onresize = function () {
        myChart.resize();
      };
    }
  },
  mounted() {
    this.ehcart_init();
  }
}
</script>


